import React, { Component } from 'react';
import { Redirect } from "react-router-dom";
import {tblUser} from '../Firebaseconnect';

class Register extends Component {
  
  componentWillMount() {
    this.getUser();
    
  }
  
  constructor(props) {
    super(props);
    this.state ={
      isRedirect : false,
      tempValue:'',
      tempUser:null,
    }
  }
  isChange = (event) => {
    const ten = event.target.name;
    const giatri = event.target.value;
    // console.log(ten);
    // console.log(giatri);
    this.setState({
      [ten]:giatri
    });
          
  }
  updateUser(data){
     //console.log(data);
     let name = data.fname+" "+ data.lname;
     let email = data.emailverify;
     let password = data.userpassword
     tblUser.push({
     name:name,
     email: email,
     password:password,
     isStatus:0,
    }); 
     console.log(name);
  }
  getUser(){
    tblUser.on('value',(dulieu) => {
        this.setState({
        tempUser:dulieu.val(),
      });
  })
  }
  submitForm = (event) => {
    event.preventDefault();
   // console.log("email: "+this.state.emailverify);
    let indexStr = this.checkStr(this.state.emailverify,this.state.tempUser);
    //console.log(indexStr);
    if(indexStr===-1){
      //cập nhật vào csdl
     this.updateUser(this.state);
     console.log('tk đã được cập nhật');
    }else {
      console.log('email đã tồn tại');
    }

    // lấy được thông tin nhập từ form
    //   console.log(this.state);
    // Kiểm tra dữ liệu xem hợp lệ chưa
    
    // gửi email cho khách hàng
    
    // var indexOfFirst = paragraph.indexOf(searchTerm);
    
    // var pos = this.state.tempUser.indexOf('b');
    // chuyển đến trang thông báo đăng ký thành công
    // this.setState({isRedirect:true});
    
  }
  checkStr(str,data){
    var strUser = JSON.stringify(data)
    var indexOfFirst = strUser.indexOf(str);
    return indexOfFirst;
  }
    render() {
      //this.props.ketnoi();
      //console.log(this.props.hienthiForm);
      console.log(this.state);
      if (this.state.isRedirect===true) {
        return <Redirect to="/confirmation" />;
      }
        return (
            <div className="page-wrapper">
            <div className="page-inner bg-brand-gradient">
              <div className="page-content-wrapper bg-transparent m-0">
                <div className="height-10 w-100 shadow-lg px-4 bg-brand-gradient">
                  <div className="d-flex align-items-center container p-0">
                    <div className="page-logo width-mobile-auto m-0 align-items-center justify-content-center p-0 bg-transparent bg-img-none shadow-0 height-9">
                      <a href="/" className="page-logo-link press-scale-down d-flex align-items-center">
                        <img src="img/logo.png" alt="SmartAdmin WebApp" aria-roledescription="logo" />
                        <span className="page-logo-text mr-1">SmartAdmin WebApp</span>
                      </a>
                    </div>
                    <span className="text-white opacity-50 ml-auto mr-2 hidden-sm-down">
                      Already a member?
                    </span>
                    <a href="page_login-alt.html" className="btn-link text-white ml-auto ml-sm-0">
                      Secure Login
                    </a>
                  </div>
                </div>
                <div className="flex-1" style={{background: 'url(img/svg/pattern-1.svg) no-repeat center bottom fixed', backgroundSize: 'cover'}}>
                  <div className="container py-4 py-lg-5 my-lg-5 px-4 px-sm-0">
                    <div className="row">
                      <div className="col-xl-12">
                        <h2 className="fs-xxl fw-500 mt-4 text-white text-center">
                          Register now, its free!
                          <small className="h3 fw-300 mt-3 mb-5 text-white opacity-60 hidden-sm-down">
                            Your registration is free for a limited time. Enjoy SmartAdmin on your mobile, desktop or tablet.
                            <br />It is ready to go wherever you go!
                          </small>
                        </h2>
                      </div>
                      <div className="col-xl-6 ml-auto mr-auto">
                        <div className="card p-4 rounded-plus bg-faded">
                          <div className="alert alert-primary text-dark" role="alert">
                            <strong>Heads Up!</strong> Due to server maintenance from 9:30GTA to 12GTA, the verification emails could be delayed by up to 10 minutes.
                          </div>
                          <form id="js-login" noValidate action="/">
                            <div className="form-group row">
                              <label className="col-xl-12 form-label" htmlFor="fname">Your first and last name</label>
                              <div className="col-6 pr-1">
                                <input type="text" onChange={(event)=>this.isChange(event)} name="fname" id="fname" className="form-control" placeholder="First Name" required />
                                <div className="invalid-feedback">No, you missed this one.</div>
                              </div>
                              <div className="col-6 pl-1">
                                <input type="text" onChange={(event)=>this.isChange(event)} name="lname" id="lname" className="form-control" placeholder="Last Name" required />
                                <div className="invalid-feedback">No, you missed this one.</div>
                              </div>
                            </div>
                            <div className="form-group">
                              <label className="form-label" htmlFor="emailverify">Email will be needed for verification and account recovery</label>
                              <input type="email" onChange={(event)=>this.isChange(event)} name="emailverify" id="emailverify" className="form-control" placeholder="Email for verification" required />
                              <div className="invalid-feedback">No, you missed this one.</div>
                              <div className="help-block">Your email will also be your username</div>
                            </div>
                            <div className="form-group">
                              <label className="form-label" htmlFor="userpassword">Pick a password: <br />Don't reuse your bank password, we didn't spend a lot on security for this app.</label>
                              <input type="password" onChange={(event)=>this.isChange(event)} name="userpassword" id="userpassword" className="form-control" placeholder="minimm 8 characters" required />
                              <div className="invalid-feedback">Sorry, you missed this one.</div>
                              <div className="help-block">Your password must be 8-20 characters long, contain letters and numbers, and must not contain spaces, special characters, or emoji.</div>
                            </div>
                            <div className="form-group demo">
                              <div className="custom-control custom-checkbox">
                                <input type="checkbox" className="custom-control-input" onChange={(event)=>this.isChange(event)}  name="terms" id="terms" required />
                                <label className="custom-control-label" htmlFor="terms"> I agree to terms &amp; conditions</label>
                                <div className="invalid-feedback">You must agree before proceeding</div>
                              </div>
                              <div className="custom-control custom-checkbox">
                                <input type="checkbox" className="custom-control-input" onChange={(event)=>this.isChange(event)} name="newsletter" id="newsletter" />
                                <label className="custom-control-label" htmlFor="newsletter">Sign up for newsletters (dont worry, we won't send so many)</label>
                              </div>
                            </div>
                            <div className="row no-gutters">
                              <div className="col-md-4 ml-auto text-right">
                                <button id="js-login-btn" type="reset" onClick={(event)=>this.submitForm(event)} className="btn btn-block btn-danger btn-lg mt-3">Send verification</button>
                              </div>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="position-absolute pos-bottom pos-left pos-right p-3 text-center text-white">
                    2019 © SmartAdmin by&nbsp;<a href="https://www.gotbootstrap.com" className="text-white opacity-40 fw-500" title="gotbootstrap.com">gotbootstrap.com</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
        );
    }
}

export default Register;