import React, { Component } from 'react';

class Confirmation extends Component {
    render() {
        return (
            <div className="page-wrapper">
  <div className="page-inner bg-brand-gradient">
    <div className="page-content-wrapper bg-transparent m-0">
      <div className="height-10 w-100 shadow-lg px-4 bg-brand-gradient">
        <div className="d-flex align-items-center container p-0">
          <div className="page-logo width-mobile-auto m-0 align-items-center justify-content-center p-0 bg-transparent bg-img-none shadow-0 height-9">
            <a href="/" className="page-logo-link press-scale-down d-flex align-items-center">
              <img src="img/logo.png" alt="SmartAdmin WebApp" aria-roledescription="logo" />
              <span className="page-logo-text mr-1">SmartAdmin WebApp</span>
            </a>
          </div>
          <span className="text-white opacity-50 ml-auto mr-2 hidden-sm-down">
            Already a member?
          </span>
          <a href="page_login-alt.html" className="btn-link text-white ml-auto ml-sm-0">
            Secure Login
          </a>
        </div>
      </div>
      <div className="flex-1" style={{background: 'url(img/svg/pattern-1.svg) no-repeat center bottom fixed', backgroundSize: 'cover'}}>
        <div className="container py-4 py-lg-5 my-lg-5 px-4 px-sm-0">
          <div className="row">
            <div className="col-xl-12">
              <h2 className="fs-xxl fw-500 mt-4 text-white text-center">
                Thank you registrering! Please check your email.
                <small className="h3 fw-300 mt-3 mb-5 text-white opacity-70 hidden-sm-down">
                  We’ve sent a message to <strong>drlantern@gotbootstrap.com</strong> with a link to activate your account.
                </small>
              </h2>
            </div>
            <div className="col-xl-6 ml-auto mr-auto">
              <div className="card p-4 rounded-plus bg-faded">
                <div className="alert alert-primary text-dark" role="alert">
                  <strong>Heads Up!</strong> Due to server maintenance from 9:30GTA to 12GTA, the verification emails could be delayed by up to 10 minutes.
                </div>
                <a href="/" className="h4">
                  <i className="fal fa-chevron-right mr-2" /> Didn’t get an email?
                </a>
              </div>
            </div>
          </div>
        </div>
        <div className="position-absolute pos-bottom pos-left pos-right p-3 text-center text-white">
          2019 © SmartAdmin by&nbsp;<a href="https://www.gotbootstrap.com" className="text-white opacity-40 fw-500" title="gotbootstrap.com" >gotbootstrap.com</a>
        </div>
      </div>
    </div>
  </div>
</div>

        );
    }
}

export default Confirmation;