import React, { Component } from 'react';
import {Link} from "react-router-dom";

class Forget extends Component {
    _testJson = () => {
        // giả sử ta lấy được 1 chuỗi JSON như sau:
        // Lưu ý nếu JSON trả về dạng chuỗi phải thực hiện trả về đối tượng bằng hàm
        // JSON.parse(tenbien)
        
        let dulieu ={
            0: {id: "1", email: "tungocvan@yahoo.com", name: "Lê Vân"},
            1: {id: "-Lmc9qDam9EpuRkuHCyi", email: "vansala78@gmail.com", name: "Nguyễn Văn B"},
            2: {id: "-LmcA3nAh_vUj-cUPBep", email: "tungocvan@gmail.com", name: "Từ Ngọc Vân"},
            3: {id: "-LmcAHHv631euNXF03KX", email: "tungocvan@gmail.com", name: "Từ Ngọc Vân"},
        };
      // console.log(dulieu); // object : object
       var keyDulieu = [];
       var itemsObj=[];
       for (let key in dulieu) {
         keyDulieu.push(key); // lưu key gốc
         itemsObj.push(dulieu[key]); 
       }
  //     console.log(keyDulieu); // in được key gốc
       
    //   console.log(itemsObj); // array : object
      
       var filedItems=[];
       //  console.log(itemsObj[0]);  // obj
       var fieldName = itemsObj[0];
       for (let key in fieldName) {
           filedItems.push(key);
       }
   //    console.log(filedItems); // In ra được các field 
   // đỗ dữ liệu ra
       return itemsObj.map((value,key) => {
        console.log(value.id +" - " + value.name +" - " + value.email);
          return(
            <div key={key}>  
                 <p>{value.id}{' - '}{value.name}{' - '}{value.email}</p>
             
            </div>
          );
       
       });
       
    }
    // render begin
    render() {
        return (
            <div>
                Reset lại mật khẩu
                <hr/>
                {this._testJson()}
                <hr/>
                <Link to={"/login"}>Login</Link>
            </div>
        );
    }
}

export default Forget;