import React, { Component } from 'react';
import {BrowserRouter,Route} from 'react-router-dom';
import {connect} from 'react-redux';
import Home from './Components/Home';
import Login from './Components/Login';
import Register from './Components/Register';
import Confirmation from './Components/Confirmation';
import Forget from './Components/Forget';
import './App.css';

export class App extends Component {
  
  render() {
    return (
      <BrowserRouter>
       <div>
       </div> 
       <Route component={Register} exact path="/"></Route>
       <Route component={Login} exact path="/login"></Route>
       <Route component={Home} exact path="/home"></Route>
       <Route component={Register} exact path="/register"></Route>
       <Route component={Confirmation} exact path="/confirmation"></Route>
       <Route component={Forget} exact path="/forget"></Route>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    name: state.name,
    age: state.age,
    color:state.color,
    sothich:state.sothich
  }
}
// this.props.testStore
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    addData: (getItem) => {
      dispatch({type:"ADD_DATA",getItem})
    },
    getData: (getItem) => {
      dispatch({type:"getData",getItem})
    }
  }
}
// this.props.addData()

export default connect(mapStateToProps, mapDispatchToProps)(App);
