import React, { Component } from 'react';
import { Redirect } from "react-router-dom";
import {connect} from 'react-redux';

export class Home extends Component {
    componentWillMount(){
        console.log(this.props.isHome);
        if(this.props.isHome){
            localStorage.setItem('isHome',this.props.isHome);
        }
        
    }
    constructor(props) {
        super(props);
        this.state = {
            idSub:-1,
            idItem:"",
            isHome:false
        }
    }
    logOut(event){
        
        //event.preventDefault();
        this.props.isHomeFunc();
        console.log('Đăng xuất thành công ');
        localStorage.removeItem('isHome');
        return <Redirect to="/" />;
    }
    openNav(event){
          event.preventDefault();
          console.log('open');
          document.getElementById("mySidebar").style.width = "250px";
          document.getElementById("main").style.marginLeft = "250px";
     }
    closeNav(event){
        event.preventDefault();
        console.log('close');
        document.getElementById("mySidebar").style.width = "0";
        document.getElementById("main").style.marginLeft= "0";
    }
    openMenuItem(event,id){
        event.preventDefault(); 
        if(this.state.idItem) {
            document.getElementById(this.state.idItem).style.display="";   
        }
        document.getElementById(id).style.display="block";
        this.setState({idItem:id});
    }
    openMenu(event,key){
        event.preventDefault();        
        var dropdown = document.getElementsByClassName("dropdown-btn");
        let item = dropdown[0].nextElementSibling.className ;
        var items = document.getElementsByClassName(item);
             
        if(key !== this.state.idSub){
           if(this.state.idSub > -1){
            items[this.state.idSub].style.display="" ;
           }
            if(items[key].style.display==="block"){
                items[key].style.display="" ;
            } else {
                items[key].style.display="block" ;
            }
        } 
        
        
        if(this.state.idSub === key){
                     
            if(items[key].style.display==="block"){
                items[key].style.display="" ;
            } else {
                items[key].style.display="block" ;
            }
         } 
        this.setState({idSub:key});
    }

    Showmenu(){
        return(
            <div>
            <a href="/" onClick={(event,id)=>this.openMenuItem(event,"about")}>About</a>
            <a href="/" onClick={(event,id)=>this.openMenuItem(event,"services")}>Services</a>
            <a href="/">Clients</a>

            <a href="/" className="dropdown-btn" onClick={(event,key)=>this.openMenu(event,0)}>Dropdown 
            <i className="fa fa-caret-down"></i>
            </a>
            <div className="dropdown-container">
                <a href="/">Link 1</a>
                <a href="/">Link 2</a>
                <a href="/">Link 3</a>
            </div>

            <a href="/" className="dropdown-btn" onClick={(event,key)=>this.openMenu(event,1)}>Dropdown 1 
            <i className="fa fa-caret-down"></i>
            </a>
            <div className="dropdown-container">
                <a href="/">Link 1</a>
                <a href="/">Link 2</a>
                <a href="/">Link 3</a>
            </div>

            <a href="/" className="dropdown-btn" onClick={(event,key)=>this.openMenu(event,2)}>Dropdown 2
            <i className="fa fa-caret-down"></i>
            </a>
            <div className="dropdown-container">
                <a href="/">Link 1</a>
                <a href="/">Link 2</a>
                <a href="/">Link 3</a>
            </div>
            <a href="/" onClick={(event)=>this.logOut(event)} >Đăng Xuất</a>
            </div>
        );
    }
    render() {
       console.log(localStorage.getItem('isHome'));
        
        if(!localStorage.getItem('isHome')){
            return <Redirect to="/login" />;
        }
        return (
            <div>
  <div id="mySidebar" className="sidebar">
    <a href="/" className="closebtn" onClick={(event)=>this.closeNav(event)}>×</a>
    {this.Showmenu()}

  
  </div>
  <div id="main">
    <button className="openbtn" onClick={(event)=>this.openNav(event)}>☰ Toggle Sidebar</button>  
    <div id="about">
    <h1>Trang About</h1>    
    <h2>Collapsed Sidebar</h2>
    <p>Click on the hamburger menu/bar icon to open the sidebar, and push this content to the right.</p>
    </div>       
    <div id="services">
    <h1>Trang Services</h1>    
    <h2>Collapsed Sidebar</h2>
    <p>Click on the hamburger menu/bar icon to open the sidebar, and push this content to the right.</p>
    </div> 
  </div>
</div>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
      isHome: state.isHome,
      
    }
  }
  // this.props.testStore
  
  const mapDispatchToProps = (dispatch, ownProps) => {
    return {
      isHomeFunc: () => {
        dispatch({type:"IS_HOME"})
      }
    }
  }
  // this.props.addData()
  
  export default connect(mapStateToProps, mapDispatchToProps)(Home);
